import React, { useState, useEffect, useRef } from "react";
import Item from "./Item.js";
import axios from "axios";
import { checkedItemValue, checkedItemKey } from "./helpers";
import { useSelector, useDispatch } from "react-redux";
import { loadNextPageToGroup, setNextAPIPageForGroup } from "../store/Reducer";
import { defaultGroups } from "./helpers/Defaults";

/* let test = 0;
 */
function ItemList() {
	const currentGroupName = useSelector((state) => state.currentGroup);
	const data = useSelector((state) => state.data);
	const loading = useSelector((state) => state.loading);
	const nextPageForGroup = useSelector((state) => state.nextPageForGroup[currentGroupName]);
	const dispatch = useDispatch();
	const loadNextDataToGroupInStore = (nextPageData, groupName) =>
		dispatch(loadNextPageToGroup(nextPageData, groupName));
	const setNextGroupPageOnStore = (nextPage, groupName) =>
		dispatch(setNextAPIPageForGroup(nextPage, groupName));

	const [moreIsLoading, setMoreIsLoading] = useState(false);
	const [blink, setBlink] = useState(true);

	useEffect(() => {
		if (!data[currentGroupName]) {
			setTimeout(() => {
				setBlink((blink) => !blink);
				/* console.log(test); */
			}, 800);
		}
	}, [blink]);

	/* test = test + 1; */

	// lazy scrolling
	const [observerFor, setObserverFor] = useState(defaultGroups());

	let loadMoreRef = useRef(null);
	console.log("OUT nextPageForGroup is ", nextPageForGroup);

	/* 	function onLoadMoreVisable(entries) {
		console.log("CLOSURE nextPageForGroup is ", onLoadMore);
		entries.forEach((entrie) => {
			if (entrie.isIntersecting) {
				onLoadMore();
			}
		});
	}
 */
	/* 	useEffect(() => {
		if (loadMoreRef.current) {
			if (observerFor[currentGroupName]) {
				console.log(loadMoreRef.current);
				observerFor[currentGroupName].observe(loadMoreRef.current);
			} else {
				setObserverFor((oldObservers) => {
					const newObservers = { ...oldObservers };
					newObservers[currentGroupName] = new IntersectionObserver(
						(entries) => onLoadMoreVisable(entries),
						{
							threshold: 0.5,
						}
					);
					return newObservers;
				});
			}
		}
	});
 */
	function onLoadMore() {
		setMoreIsLoading(true);
		console.log(nextPageForGroup);
		axios
			.get(nextPageForGroup)
			.then((res) => {
				processDataFromAPI(res.data);
			})
			.catch((res) => console.log("err", res));

		function processDataFromAPI(returnedData) {
			/* const nextPageData = returnedData; */
			loadNextDataToGroupInStore(returnedData.results, currentGroupName);
			setNextGroupPageOnStore(returnedData.next, currentGroupName);
			/* console.log("setting ", returnedData.next);
			setTimeout(() => console.log("instead  ", nextPageForGroup), 1000); */
			setMoreIsLoading(false);
		}
	}

	/* useEffect(() => {
		if (!observerFor[currentGroupName]) {
			setObserver(new IntersectionObserver(onLoadMoreVisable, { threshold: 0.5 }));
		}
	}, []);
 */
	function displayMode() {
		if (data[currentGroupName] !== null) {
			return (
				<>
					<ul className='item-list'>
						{data[currentGroupName].map((item) => (
							<Item key={checkedItemKey(item)} item={checkedItemValue(item)} />
						))}
					</ul>
					{nextPageForGroup ? (
						<button /* ref={loadMoreRef} */ onClick={() => onLoadMore()} className='load-more item'>
							{moreIsLoading ? "loading more..." : "-> Load more"}
						</button>
					) : null}
				</>
			);
		} else if (loading) {
			return null;
		} else {
			return (
				<div className='items-loading-screen' style={{ visibility: blink ? "visible" : "hidden" }}>
					Please select category
				</div>
			);
		}
	}

	return (
		<div>
			{loading ? <div className='items-loading-screen'>loading...</div> : null}
			<>{displayMode()}</>
		</div>
	);
}

export default ItemList;

